<template>
  <div class="row">
    <a href="#" class="card-link">
      <div class="col-sm-12 col-md-6 col-lg-4 blog-card" v-for="item in items" :key="item.id">
        <img v-bind:src="item.image" class="post-image">
        <div class="article-details">
          <h4 class="post-category">{{item.category}}</h4>
          <h3 class="post-title">{{item.title}}</h3>
          <p class="post-description">{{item.description}}</p>
          <p class="post-author">{{item.author}}</p>
        </div>
      </div>
    </a>
  </div>
</template>

<script>
import jsonData from './JsonFiles/test2'

export default {
  name: 'Test2',
  data () {
    return {
      items: []
    }
  },
  mounted () {
    this.items = jsonData
  }
}
</script>



