<template>
  <div class="table-wrap coltype">
    <table class="table table-bordered table-striped">
      <thead>
      <tr class="mb-2 bg-info text-white">
        <th scope="col" v-for="column in data.columns" :key="column.field">{{ column.label }}</th>
      </tr>
      </thead>
      <tbody>
        <tr class="head" v-for="row in data.rows" :key="row.title.order" v-on:click="changeContents(row.title.order-1)">
          <td>{{row.title.order}}</td>
          <td>{{row.title.type}}</td>
          <td>{{row.title.question}}</td>
        </tr>
        <tr class="body active" >
          <td colspan="3">
            <div class="answer img-preview" ref="cont0">
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import jsonData from './JsonFiles/test1'

export default {
  name: 'Test1',
  data () {
    return {
      data: {
        columns: [],
        rows: []
      },
      selectRow: 0
    }
  },
  mounted () {
    this.data.columns = [ ...jsonData.columns ]
    this.data.rows = [ ...jsonData.rows ]
  },
  methods: {
    changeContents: function (num) {
      this.$refs.cont0.innerHTML = this.data.rows[num].content
    }
  }
}
</script>



