<template>
  <div class="table-wrap coltype">
    <input class="form-control m-0" v-model="data.num">
    <table class="table table-bordered table-striped">
      <thead>
      <tr class="mb-2 bg-info text-white">
        <th scope="col" v-for="column in data.columns" :key="column.field">{{ column.label }}</th>
      </tr>
      </thead>
      <tbody>
        <tr class="header" :key="data.rows.id" v-if="data.num">
          <td>{{data.rows[data.num-1].order}}</td>
          <td>{{data.rows[data.num-1].type}}</td>
          <td>{{data.rows[data.num-1].question}}</td>
        </tr>
        <tr class="header" v-for="row in data.rows" :key="row.id" v-else>
          <td>{{row.order}}</td>
          <td>{{row.type}}</td>
          <td>{{row.question}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import jsonData from './JsonFiles/test4'

export default {
  name: 'Test4',
  data () {
    return {
      data: {
        columns: [],
        rows: [],
        num: 1
      },
      search: ''
    }
  },
  mounted () {
    this.data.columns = [ ...jsonData.columns ]
    this.data.rows = [ ...jsonData.rows ]
  },
  computed: {
    rows () {
      return this.data.rows
    }
  }
}
</script>



