<template>
  <div>
    <div class="row mb-1" v-for="item in items" :key="item.id">
      <div class="col-sm-2">{{item.title}}</div>
      <div class="col-sm-10 pt-1">
        <div class="progress">
          <div v-bind:style="{backgroundColor:item.color, width:item.size}"  >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import jsonData from './JsonFiles/test3'
export default {
  name: 'Test3',
  data () {
    return {
      items: []
    }
  },
  mounted () {
    this.items = jsonData
  }
}
</script>



